import { useDispatch, useSelector } from 'react-redux';
import { addContact } from 'redux/operations';
import { getContactsList } from 'redux/selectors';
import Notiflix from 'notiflix';
import {
  Form,
  BoxName,
  BoxNumber,
  Input,
  Label,
  Button,
} from './ContactForm.styled';

export default function ContactForm() {
  const contacts = useSelector(getContactsList);
  const dispatch = useDispatch();

  const hendleSubmit = e => {
    e.preventDefault();
    const form = e.currentTarget;
    let name = e.currentTarget.elements.name.value;
    let number = e.currentTarget.elements.number.value;
    const newContact = { name, number };
    if (
      contacts.find(
        contact => contact.name.toLowerCase() === newContact.name.toLowerCase()
      )
    ) {
      console.log(' ');
      Notiflix.Report.info(
        'INFO',
        `${newContact.name} already in the phonebook`
      );
      return;
    } else if (contacts.find(contact => contact.number === newContact.number)) {
      console.log(' ');
      Notiflix.Report.info(
        'INFO',
        `${newContact.number} already in the phonebook`
      );
      return;
    }
    Notiflix.Notify.success(
      `${newContact.name} This subscriber is added to the phone book`
    );
    dispatch(addContact(newContact));
    form.reset();
  };

  return (
    <Form onSubmit={hendleSubmit}>
      <BoxName>
        <Label>
          Name:
          <Input
            type="text"
            name="name"
            pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
            title="     , ,   .  Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan  . ."
            required
          />
        </Label>
      </BoxName>
      <BoxNumber>
        <Label>
          Number:
          <Input
            type="tel"
            name="number"
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="        , ,       +"
            required
          />
        </Label>
      </BoxNumber>
      <Button type="submit">Add contact</Button>
    </Form>
  );
}
